.container-fluid {
  display: flex;
  height: 100vh;
  flex-direction: column;
}

.row {
  padding-left: 20px;
  padding-right: 20px;
}

.header {
  text-align: center;
  color: #fff;
  margin-left: 10px;
  margin-right: 10px;

}

.header > h1 {
  font-family: 'Roboto', sans-serif;
  font-weight: 100;
  margin-top: 15px;
  margin-bottom: 30px;
}

.MarkdownInput,
.MarkdownOutput {
  box-sizing: border-box;
  padding: 0;
}

.MarkdownInput > textarea,
.MarkdownOutput > div {
  box-sizing: border-box;
  width: 100%;
  height: 80vh;
  padding: 5px;
  word-wrap: break-word;
  border: none;
  overflow-y: scroll;
  box-shadow: 0 1px 2px rgba(0,0,0,0.12), 0 1px 1px rgba(0,0,0,0.24);
}

.MarkdownInput > textarea {
  resize: none;
  background-color: rgba(109, 104, 117, 0.3);
  color: #fff;
  font-family: 'Inconsolata', monospace;
  &:focus {
    border: none;
    outline: none;
  }
}

.MarkdownOutput > div {
  background-color: #f9f9f9;
  font-family: 'Roboto', sans-serif !important;
  font-weight: 400;
  hr {
    border-top: 1px solid #ddd;
  }
  blockquote {
    border-left: 5px solid #ddd;
  }
  img {
    max-width: 100px;
    max-height: 100px;
  }
}

.credits {
  margin-top: auto;
  padding: 5px;
  font-size: 14px;
  text-align: center;
}
.credits-link,
.credits-link:active,
.credits-link:visited {
  font-family: 'Roboto', sans-serif;
  color: #b5838d;
  text-decoration: none;
  -moz-transition: color 0.3s;
  -o-transition: color 0.3s;
  -webkit-transition: color 0.3s;
  transition: color 0.3s;
}
.credits-link:hover {
  color: #97656f;
  text-decoration: none;
}